@use 'base/variables' as v;

.alert{
  padding: calc(v.$separacion / 4);
  color: v.$blanco;
  width: 100%;
  display: block;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: v.$bold;

  &.error{
    background-color: v.$rojo;
  }

  &.success{
    background-color: v.$verde;
  }
}