/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.page-name {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.page-description {
  text-align: center;
}

.app-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  min-height: 100vh;
}
@media (max-width: 920px) {
  .app-container {
    grid-template-columns: 1fr 2fr;
  }
}
@media (max-width: 768px) {
  .app-container {
    grid-template-columns: 1fr;
  }
}
.app-container .image {
  background-image: url("../img/1.webp");
  background-size: cover;
  background-position: center center;
}
@media (max-width: 768px) {
  .app-container .image {
    min-height: 20rem;
    height: auto;
  }
}
.app-container .app {
  width: 90%;
  margin: 0 auto;
  padding: 3rem 0;
}
@media (max-width: 768px) {
  .app-container .app {
    padding: 0;
  }
}

.section {
  padding: 4rem 0;
  display: none;
}
.section .text-center {
  text-align: center;
}

.show {
  display: block;
}

.hide {
  display: none;
}

.opacity0 {
  opacity: 0;
  visibility: hidden;
}

.services-list {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 768px) {
  .services-list {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
.services-list .service {
  background-color: #FFFFFF;
  padding: 3rem;
  text-align: center;
  border: none;
  border-radius: 1rem;
  transition: transform 0.3s ease;
}
.services-list .service:hover {
  transform: scale(1.05);
  cursor: pointer;
}
.services-list .service p {
  color: #1a1b15;
  margin: 0 0 1.25rem;
}
.services-list .service .price-service {
  color: #0da6f3;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}
.services-list .service.selected {
  background-color: #0da6f3;
  color: #FFFFFF;
  transition: all 0.3s ease;
  backface-visibility: hidden;
}
.services-list .service.selected p {
  color: #FFFFFF;
}
.services-list .service.selected .price-service {
  color: #FFFFFF;
}

.summary-content p {
  margin-bottom: 1rem;
}
.summary-content span {
  color: #0da6f3;
  font-weight: 700;
  font-size: 2.4rem;
}

.service-container {
  padding: 2rem;
  border-bottom: 1px solid #e1e1e1;
}
.service-container:last-of-type {
  margin-bottom: 5rem;
}

.form {
  width: 100%;
  margin-top: 5rem;
}
.form .field {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.form .field label {
  flex: 0 0 10rem;
}
.form .field input {
  flex: 1;
  border: none;
  outline: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.form .field input:disabled {
  background-color: #898e6e;
  color: #FFFFFF;
  cursor: not-allowed;
}

.button {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: auto;
  margin: 5rem 0;
}
.button:hover {
  background-color: #0a85c3;
  cursor: pointer;
}
@media (max-width: 768px) {
  .button {
    width: 100%;
  }
}

.actions {
  margin-top: 5rem;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .actions {
    flex-direction: column;
  }
}
.actions a {
  color: #FFFFFF;
  margin-bottom: 2.5rem;
}

.alert {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 700;
}
.alert.error {
  background-color: #cb0000;
}
.alert.success {
  background-color: #329f00;
}

.tabs {
  display: flex;
}
@media (max-width: 768px) {
  .tabs {
    flex-direction: column;
  }
}
.tabs button {
  background-color: #0da6f3;
  color: #FFFFFF;
  width: 100%;
  padding: 1rem;
  border: none;
  outline: none;
  font-weight: 700;
  text-transform: uppercase;
  transition: background-color 0.3s ease;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #0a85c3;
}
.tabs button.current {
  background-color: #FFFFFF;
  color: #0da6f3;
}
@media (max-width: 768px) {
  .tabs button {
    display: block;
    width: 80%;
    margin: 0 auto 0.7rem auto;
  }
}

.pagination {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .pagination {
    flex-direction: column;
  }
  .pagination button {
    margin: 0.7rem 0;
    padding: 1rem 0rem;
  }
}