@use 'base/variables' as v;
@use 'base/mixins' as m;

.form{
  width: 100%;
  margin-top: v.$separacion;

  .field{
    display: flex;
    margin-bottom: 2rem;
    align-items: center;

    label{
      flex: 0 0 10rem;
    }

    input{
      flex: 1;
      border: none;
      outline: none;
      padding: calc(v.$separacion / 4);
      border-radius: 1rem;

      &:disabled{
        background-color: lighten(v.$negro, 40%);
        color: v.$blanco;
        cursor: not-allowed;
      }
    }

  }
}